  const [state, setState] = useReducer(
    (state, newState) => ({ ...state, ...newState }),
    {
      iframeLoaded: false,
      peaks: [],
      waveColor: '#cccccc',
      isLoaded: false
    }
  );

  const waveformRef = useRef();
  useEffect(() => {
    if (waveformRef.current) {
      initWavesurfer(waveformRef.current);
    }
  }, []);

  useEffect(() => {
    getWaveForm();
  }, [player.currentTrack]);

